/* Project images */
.project-image {
  border-radius: 0.5%;
  box-shadow: 0 0.5em 1em -0.125em rgb(10 10 10 / 10%),
    0 0 0 1px rgb(10 10 10 / 2%);
  transition: all 0.35s ease-in-out;
}

.project-image:hover {
  transform: scale(1.05);
}

/* Horizontal lines */
.main-hr {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.project-hr {
  border: 0;
  height: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.283);
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

/* Project description */
.project-description {
  /* font-family: "Lora", serif; */
  font-size: large;
}

/* Project technologies */
.project-technologies {
  border-radius: 25px !important;
  background-color: #fb632a;
  color: white;
  font-size: 12px;
}

.project-technologies:hover {
  color: white;
  cursor: auto !important;
}

/* Buttons */
.app-button {
  background-color: transparent;
  border: solid 2px #2f7fff;
  color: #2f7fff;
  transition: 0.7s;
}

.app-button:hover {
  border: solid 2px #2f7fff;
  background-color: #2f7fff;
  color: whitesmoke;
  transition: 0.7s;
}

.github-button {
  background-color: transparent;
  border: solid 2px #363636;
  color: #363636;
  transition: 0.7s;
}

.github-button:hover {
  border: solid 2px #363636;
  background-color: #363636;
  color: whitesmoke;
  transition: 0.7s;
}
